import fs from 'fs';
import path from 'path';
import matter from 'gray-matter';
import { remark } from 'remark';
import html from 'remark-html';
import { Project } from '@/types/project';

const projectsDirectory = path.join(process.cwd(), 'projects');

export function getAllProjects(): Project[] {
  // Obtener nombres de archivos de la carpeta projects
  const fileNames = fs.readdirSync(projectsDirectory);
  
  const allProjectsData = fileNames
    .filter((fileName) => fileName.endsWith('.md'))
    .map((fileName) => {
      // Remover ".md" del nombre del archivo para obtener el slug
      const slug = fileName.replace(/\.md$/, '');

      // Leer el archivo markdown como string
      const fullPath = path.join(projectsDirectory, fileName);
      const fileContents = fs.readFileSync(fullPath, 'utf8');

      // Usar gray-matter para parsear el frontmatter del project
      const matterResult = matter(fileContents);

      // Combinar los datos con el slug
      return {
        slug,
        title: matterResult.data.title,
        date: matterResult.data.date,
        content: matterResult.content,
        excerpt: matterResult.data.excerpt,
      };
    });

  // Ordenar projects por fecha y hora de creación (más recientes primero)
  return allProjectsData.sort((a, b) => {
    const dateA = new Date(a.date);
    const dateB = new Date(b.date);
    
    if (dateA < dateB) {
      return 1;
    } else if (dateA > dateB) {
      return -1;
    } else {
      return 0;
    }
  });
}

export function getProjectsWithPagination(page: number = 1, limit: number = 8): { projects: Project[], totalProjects: number, totalPages: number, currentPage: number } {
  const allProjects = getAllProjects();
  const totalProjects = allProjects.length;
  const totalPages = Math.ceil(totalProjects / limit);
  const startIndex = (page - 1) * limit;
  const endIndex = startIndex + limit;
  const projects = allProjects.slice(startIndex, endIndex);

  return {
    projects,
    totalProjects,
    totalPages,
    currentPage: page
  };
}

export function getProjectBySlug(slug: string): Project | null {
  try {
    const fullPath = path.join(projectsDirectory, `${slug}.md`);
    const fileContents = fs.readFileSync(fullPath, 'utf8');

    // Usar gray-matter para parsear el frontmatter del project
    const matterResult = matter(fileContents);

    // Usar remark para convertir markdown a HTML
    const processedContent = remark()
      .use(html)
      .processSync(matterResult.content);
    const contentHtml = processedContent.toString();

    // Combinar los datos con el slug y el contenido HTML
    return {
      slug,
      title: matterResult.data.title,
      date: matterResult.data.date,
      content: contentHtml,
      excerpt: matterResult.data.excerpt,
    };
  } catch {
    return null;
  }
}

export function getAllProjectSlugs(): string[] {
  const fileNames = fs.readdirSync(projectsDirectory);
  return fileNames
    .filter((fileName) => fileName.endsWith('.md'))
    .map((fileName) => fileName.replace(/\.md$/, ''));
} 